@import 'ScssHelpers/helpers-all.scss';

.space_filter {
	border-radius: $gl_radius;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_2, min) {
		padding: 3.8rem var(--gl-indent) 4.8rem;
		border: 1px solid $black_2;
		box-shadow: 1rem 1rem 4rem rgba(0, 0, 0, 0.1);
	}

	@include bp($point_3) {
		padding: 1.4rem var(--gl-indent) 2.6rem;
		border: 2px solid $black_2;
		box-shadow: 1rem 1rem 1rem rgba(20, 20, 20, 0.1);
	}

	.noUi-horizontal {
		height: 1px;
	}

	.noUi-connects {
		overflow: visible;

		@include bp($point_3) {
			display: flex;
			align-items: center;
		}
	}

	.noUi-target .noUi-connect {
		position: relative;
		background: $black_2;
		height: 3px;
	}

	.noUi-target .noUi-base {
		background: $black_2;
		height: 1px;
	}

	.noUi-handle.noUi-handle-lower {
		top: 50%;

		border-radius: 50%;
		background-color: $coral;
		box-shadow: none;
		border: none;
		border: 3px solid $black_2;
		transform: translateY(-50%);

		cursor: grab;

		&:before,
		&:after {
			display: none;
		}

		@include bp($point_2, min) {
			width: 3rem;
			height: 3rem;

			box-shadow: 0.5rem 0.5rem 1rem $coral;
		}

		@include bp($point_3) {
			width: 2rem;
			height: 2rem;
		}
	}
}

.space_filter__body {
	display: flex;
	align-items: flex-end;

	max-width: 96rem;
	width: 100%;
	margin: 0 auto;
}

.space_filter__title {
	text-align: center;
	width: 100%;
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}
}

.space_filter__title_input {
	width: 100%;
	font-family: $font_2;
	text-align: center;
	border: none;
	background-color: transparent;

	@include bp($point_2, min) {
		@include h3;
	}

	@include bp($point_3) {
		font-weight: 700;
		font-size: 2rem;
		line-height: 120%;
		font-family: $font_2;
	}
}

.space_filter__wrap {
	flex: 1 1 auto;

	@include bp($point_2, min) {
		padding: 0 2.2rem 1.6rem;
	}

	@include bp($point_3) {
		padding: 0 3rem 1rem;
	}
}

.space_filter__slider_w {
	// flex: 1 1 auto;
}

.space_filter__value {
	display: block;
	@include bp($point_2, min) {
		@include h3;
		font-family: $font_2;
	}

	@include bp($point_3) {
		font-weight: 500;
		font-size: 1.6rem;
		line-height: 140%;
		letter-spacing: 0.02em;
	}
}
